.login {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    >div {
        width: 100%;
        height: 100%;
        background: url('https://img.tukuppt.com/bg_grid/00/04/98/DKtGYCCC6K.jpg!/fh/350') no-repeat;
        background-size: 100% 100%;
        padding: 20% 40%;
        filter: blur(1px);
    }
    .ant-form {
        z-index: 999;
        width: 300px;
        height: 200px;
        position: absolute;
        left: calc(50% - 150px);
        top: calc(50% - 100px);
        .ant-form-item-control {
            button {
                margin-left: 50%;
                transform: translate(-50%);
            }
        }
    }
}